@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
:root {
  --black: rgba(0, 0, 0, 1);
  --black-alhpa-1: rgba(0, 0, 0, 0.1);
  --black-alpha-2: rgba(0, 0, 0, 0.2);
  --black-alpha-3: rgba(0, 0, 0, 0.3);
  --black-alpha-6: rgba(0, 0, 0, 0.6);
  --grey: rgba(68, 68, 68, 1);
  --grey-2: rgba(236, 236, 236, 1);
  --grey-3: rgba(229, 229, 229, 1);
  --white: rgba(255, 255, 255, 1);
  --white-alpha-3: rgba(255, 255, 255, 0.3);
  --white-2: rgba(249, 249, 249, 1);
  --white-3: rgba(241, 241, 241, 1);
  --cyan: rgba(99, 209, 187, 1);
  --cyan-alpha-3: rgba(99, 209, 187, 0.3);
  --cyan-2: rgba(86, 195, 178, 1);
  --cyan-3: rgba(98, 209, 186, 1);
  --cyan-4: rgba(70, 193, 174, 1);
}

body {
  min-width: 320px;
  margin: 0;
  overflow-x: hidden;
  font-weight: 400;
  font-size: 17px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: var(--grey);
  background-color: var(--white);
}

img {
  display: block;
}

.show-main-nav {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.container {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 520px;
  padding-right: 30px;
  padding-left: 30px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    min-width: 768px;
    max-width: 968px;
    padding-right: 50px;
    padding-left: 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    max-width: 1150px;
    padding-right: 50px;
    padding-left: 50px;
  }
}

.button {
  padding: 16px 45px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  font-family: "Open Sans", "Arial", sans-serif;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid var(--grey);
  cursor: pointer;
}
.button:hover {
  color: var(--white);
  background-color: var(--grey);
}
.button:active {
  color: var(--white-alpha-3);
}

.header {
  position: relative;
  z-index: 1;
}
@media (min-width: 768px) {
  .header {
    margin-bottom: -50px;
  }
}
@media (min-width: 1150px) {
  .header {
    margin-bottom: -65px;
  }
}
@media (max-width: 767px) {
  .header::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    display: block;
    width: 200vw;
    border-bottom: 1px solid var(--black-alhpa-1);
    transform: translateX(50%);
  }
}

.header__inner {
  position: relative;
}

@media (max-width: 767px) {
  .header__logo {
    display: flex;
    justify-content: center;
    padding: 22px 0;
  }
}
@media (min-width: 768px) {
  .header__logo {
    position: absolute;
    top: 22px;
    left: 0;
  }
}
@media (min-width: 1150px) {
  .header__logo {
    position: absolute;
    top: 9px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.header__hamburger {
  position: absolute;
  top: 24px;
  right: -4px;
  width: 20px;
  height: 14px;
  padding: 15px;
  background: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  transition-delay: 0.1s, 0s;
}
@media (min-width: 768px) {
  .header__hamburger {
    display: none;
  }
}

.header__hamburger--no-js {
  display: none;
}

.header__hamburger--open:hover {
  transform: rotate(90deg);
}

.header__hamburger-inner {
  position: absolute;
  top: 45%;
  right: 5px;
  left: 5px;
  height: 2px;
  background-color: var(--black);
  transition: background 0s 0.3s;
}

.header__hamburger-inner::before,
.header__hamburger-inner::after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--black);
  transition-delay: 0.3s, 0s;
  transition-duration: 0.3s, 0.3s;
  transition-property: top, transform;
}

.header__hamburger-inner::before {
  top: -6px;
}

.header__hamburger-inner::after {
  top: 6px;
}

.header__hamburger--open .header__hamburger-inner {
  background-color: transparent;
}

.header__hamburger--open .header__hamburger-inner::before,
.header__hamburger--open .header__hamburger-inner::after {
  transition-delay: 0s, 0.3s;
}

.header__hamburger--open .header__hamburger-inner::before {
  top: 0;
  transform: scale(1.3) rotate(45deg);
}

.header__hamburger--open .header__hamburger-inner::after {
  top: 0;
  transform: scale(1.3) rotate(-45deg);
}

.footer {
  background: #f1f1f1;
}

.footer__inner {
  display: flex;
  padding: 27px 0;
}
@media (max-width: 767px) {
  .footer__inner {
