  }
}

@media (max-width: 767px) {
  .footer__logo {
    display: none;
  }
}

.footer__logo-img:hover {
  fill: var(--cyan-4);
}

.footer__logo-img:active {
  opacity: 0.3;
}

.footer__social-list {
  justify-content: center;
  margin: 0;
  padding: 0;
}
@media (max-width: 767px) {
  .footer__social-list {
    margin-bottom: 26px;
  }
}
@media (min-width: 768px) {
  .footer__social-list {
    align-items: center;
  }
}

.footer__madeby-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  color: #000000;
  text-decoration: none;
}
@media (max-width: 767px) {
  .footer__madeby-link {
    padding-top: 23px;
  }
}
@media (min-width: 768px) {
  .footer__madeby-link {
    align-items: unset;
  }
}
@media (max-width: 767px) {
  .footer__madeby-link::before {
    content: "";
    position: absolute;
    top: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: var(--grey-3);
  }
}

.footer__madeby-text {
  margin-right: 27px;
}
@media (min-width: 768px) {
  .footer__madeby-text {
    margin-top: 7px;
  }
}
@media (min-width: 1150px) {
