}

.footer__madeby-link:active .footer__madeby-img {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .logo__image {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .logo__image {
