}

.social-list__link:active {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .navigation {
    position: fixed;
    right: 0;
    left: 0;
    overflow: hidden;
    visibility: hidden;
    pointer-events: none;
  }
}
@media (min-width: 768px) {
  .navigation {
    display: grid;
    grid-template-columns: 1fr 1.1fr;
  }
}
@media (min-width: 1150px) {
  .navigation {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767px) {
  .navigation::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;
    display: block;
    width: 300vw;
    height: 300vw;
    margin-top: -150vw;
    margin-left: -150vw;
    background-color: var(--white);
    border-radius: 50%;
    transform: scale(0);
    transition: all 0.8s ease;
  }
}

.navigation--open,
.navigation--no-js {
  visibility: visible;
  pointer-events: auto;
}
.navigation--open::before,
.navigation--no-js::before {
  transform: scale(1);
  transition-duration: 0.8s;
}

.navigation--open .navigation__item,
.navigation--no-js .navigation__item {
  opacity: 1;
  transition: opacity 0.3s ease 0.3s;
}

.navigation--mobile .navigation__item {
  transition-duration: 0.3s;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .navigation__list {
    display: grid;
    grid-template-columns: auto auto;
  }
}

@media (min-width: 768px) {
  .navigation__list--user {
    position: relative;
    grid-column: 2/3;
    justify-self: end;
  }
}
@media (min-width: 768px) {
  .navigation__list--user::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    display: block;
    width: 200vw;
    border-bottom: 1px solid var(--black-alhpa-1);
    transform: translateX(50%);
  }
}

@media (min-width: 768px) {
  .navigation__list--main {
    padding-top: 16px;
    font-size: 15px;
    column-gap: 23px;
    grid-row: 2/3;
  }
}
@media (min-width: 1150px) {
  .navigation__list--main {
    justify-content: start;
    margin-left: -36px;
    padding: 0;
    font-size: 17px;
    column-gap: 0;
    grid-row: 1/2;
  }
}

.navigation__list--more {
  display: none;
}
@media (min-width: 1150px) {
  .navigation__list--more {
    display: grid;
    font-size: 15px;
    column-gap: 64px;
    justify-self: start;
  }
}

@media (max-width: 767px) {
  .navigation__item {
    position: relative;
    opacity: 0;
  }
}
@media (max-width: 767px) {
  .navigation__item::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    display: block;
    width: 200vw;
